<template>
    <div class="mblok">
       <img src="/static/mimg/blok.png" alt="">
    </div>
</template>
<script>
    export default {
        name: 'mblok',
        data() {
            return {
            }
        },
        created() {
            this.setRem(28.43);
            this.fontSize(1120);
            let useragent = navigator.userAgent.toLowerCase()
            let ismobile = /(iphone|ipod|ipad|android)/.test(useragent)
            if (!ismobile) {
                this.$router.push({ path: '/mblok' })
            }
        },
        methods: {
            setRem(itemNum) {
                //设置字体rem
                let size = itemNum || 38;
                window.onresize = remSize;
                remSize();
                function remSize() {
                    let html = document.getElementsByTagName("html")[0],
                        w = html.clientWidth;
                    w = w > 768 ? 768 : w;
                    html.style.fontSize = w / size + "px";
                }
            },
            fontSize(itemnum) {
                let html = document.getElementsByTagName("html")[0];
                let a = html.style.fontSize;
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    * {
        margin: 0;
        padding: 0;
    }
    body {
        margin: 0 !important
    }
    .mblok{
        width: 100%;
        height: 100%
    }
    .mblok img{
        width: 100%;
        height: 100%
    }
</style>


