import Vue from 'vue'
import Router from 'vue-router'
import mhome from '@/components/mhome'
import mblok from '@/components/mblok'
Vue.use(Router)
const home = (resolve) => require(['../components/home.vue'],resolve) //登录
const blog = (resolve) => require(['../components/blog.vue'],resolve) //博客
export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/m',
      name: 'mhome',
      component: mhome
    },
    {
      path: '/mblok',
      name: 'mblok',
      component: mblok
    },
    {
      path: '/blog',
      name: 'blog',
      component: blog
    }
  ]
})



